<template>
    <div class="searchAddressWarp">
        <header>
            <router-link to="">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <div>搜索</div>
        </header>
        <section>
            <div class="add-detail">
                <input type="text" placeholder="请输入小区/写字楼/学校等" v-model="addressV">
                <button @click="queBtn">确认</button>
            </div>
            <div class="warnpart">
                为了满足商家的送餐要求，建议您从列表中选择地址
            </div>
            <div class="point" v-if="isShow">
                <p>找不到地址？</p>
                <p>请尝试输入小区、写字楼或学校名</p>
                <p>详细地址（如门牌号）可稍后输入哦。</p>
            </div>
            <ul>
                <li v-for="item in addressArr" @click="chooseBtn(item)">
                    <p>{{item.name}}</p>
                    <p>{{item.address}}</p>
                </li>
            </ul>

        </section>
    </div>
</template>

<script>
    import { mapState } from "vuex";
    export default {
        name: "SearchAddress",
        data(){
            return {
                cityId:'',
                isShow:true,
                addressV:'',//搜索框的地址
                addressArr:[],//后台返回的数据
            }
        },
        created(){
            this.cityId = localStorage.getItem("cityId");
        },
        methods:{
            queBtn(){
                this.isShow = false
                this.axios.get('/v1/pois?',{
                    params:{
                        city_id:this.cityId,
                        keyword:this.addressV,
                        type:"search"
                    }
                }).then(res=>{
                    console.log(res)
                    this.addressArr = res;
                }).catch(err=>{})

            },
            chooseBtn(i){
                console.log(i)
                this.addressV = i.name;
                this.$store.state.addressInfo = i;
                localStorage.setItem("addV",i.name+'('+i.address+')');
                this.$router.push({path:"/newAddress"})
            }
        }
    }
</script>

<style scoped>
    .searchAddressWarp {
        padding-top: 1.95rem;
    }
    header {
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    header>div {
        width: 50%;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        color: #fff;
        text-align: center;
        font-size: .8rem;
        font-weight:700;
    }
    header>a {
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
        color: #fff;
    }

    .add-detail{
        display: flex;
        justify-content: space-between;
        background: #fff;
        padding: .5rem;
    }
    .add-detail input{
        outline: none;
        display: block;
        width: 11.8rem;
        padding: .4rem;
        background: #f2f2f2;
        border: 1px solid #ddd;
        border-radius: 5px;
        font-size: .6rem;
    }
    .add-detail button{
        display: block;
        width: 3rem;
        background: #3199e8;
        font-size: .7rem;
        color: #fff;
        border-radius: 5px;
        margin-left: .2rem;
    }
    .warnpart{
        background: #fff6e4;
        font-size: .62rem;
        color: #ff883f;
        text-align: center;
        padding: .2rem 0;
    }
    .point{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 100%;
    }
    .point p{
        width: 100%;
        text-align: center;
        font-size: .7rem;
        color: #969696;
        margin-bottom: .4rem;
    }
    ul{
        margin: 0;
        font-size: 0;
        color: #969696;
    }
    li>p{
        margin-bottom: .2rem;
    }
    li{
        background-color: #F2F2F2;
        font-size: .65rem;
        padding: .4rem .5rem;
        border-bottom: 1px solid #CCCCCC;
    }
</style>